@import url(https://fonts.googleapis.com/css2?family=Black+Ops+One&family=Chonburi&family=Courgette&family=Quantico:wght@700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Black+Ops+One&family=Chonburi&family=Courgette&family=Quantico:wght@700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.feedback {
  align-items: flex-start;
  background-color: #82c91e;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  min-height: 100vh;

}

.main {
  display: flex;
  flex-direction: column;
  margin: auto;
  width: 100%;
}

.div-messages {
  align-items: center;
  display: flex;
  justify-content: space-evenly;
  margin: 40px;
}

.div-msg {
  background-color: #fa5252;
  border-radius: 7px;
  color: #511f52;
  font-weight: 700;
  text-align: center;
  width: 50%;
}

.div-button {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  margin: auto;
  padding: 20px;
  text-align: center;
  width: 50%;
}

.button-ranking {
  background-color: #fa5252;
  border: none;
  border-radius: 7px;
  color: white;
  font-weight: 700;
  height: 40px;
  margin-right: 30px;
  margin-top: 10px;
  padding: 7px 14px;
  text-align: center;
  -webkit-transition: 0.3s;
  transition: 0.3s;
  width: 100%;
}

.button-ranking:hover {
  background-color: #511f52;
  -webkit-transition: 0.2s;
  transition: 0.2s;
}

.button-play-again {
  background-color: #511f52;
  border: none;
  border-radius: 7px;
  color: white;
  font-weight: 700;
  height: 40px;
  margin-top: 10px;
  padding: 7px 14px;
  -webkit-transition: 0.3s;
  transition: 0.3s;
  width: 100%;
}

.button-play-again:hover {
  background-color: #fa5252;
  -webkit-transition: 0.2s;
  transition: 0.2s;
}

[ data-testid = 'feedback-total-question' ] {
  font-family: Quantico , sans-serif;
  font-size: 20px;
  text-align: center;
}

[ data-testid = 'feedback-text' ] {
  color: white;
  font-family: Quantico , sans-serif;
  font-size: 30px;
  text-align: center;
}

[ data-testid = 'feedback-total-score' ] {
  font-family: Quantico , sans-serif;
  font-size: 20px;
  text-align: center;
}

.login-page {
  align-items: center;
  background-color: #84459c;
  display: flex;
  height: 100vh;
  justify-content: center;
  overflow-y: hidden;
}

.login-box {
  align-items: center;
  background-color: #511f52;
  border-radius: 10px;
  box-shadow: 0 2px 3px rgba(0, 0, 0, 0.2);
  display: flex;
  justify-content: space-between;
  padding: 50px 40px;
}

.left, .right {
  padding: 0 50px;
  text-align: center;
  width: 50%;
}

.right .login-title {
  color: white;
  font-size: 40px;
  text-align: center;
}

.right .login-title-span {
  color: white;
  font-size: 15px;
  margin-bottom: -50px;
  margin-top: -100px;
  text-align: center;
}

.img-login {
  -webkit-transition: 0.2s;
  transition: 0.2s;
  width: 500px;
}

.form-login {
  display: flex;
  flex-direction: column;
  margin: 30px 0;
}

.form-login input {
  background-color: #82c91e;
  border: none;
  border-radius: 7px;
  font-weight: 600;
  margin-top: 10px;
  padding: 10px;
}

input:-webkit-autofill, input:-webkit-autofill:hover, input:-webkit-autofill:focus {
  box-shadow: 0 0 0 30px #82c91e inset;
  -webkit-text-fill-color: white;
}

.form-login input:focus {
  color: white;
}

.form-login input:visited {
  color: white;
}

.form-login input::-webkit-input-placeholder {
  color: white;
  font-size: 14px;
}

.form-login input:-ms-input-placeholder {
  color: white;
  font-size: 14px;
}

.form-login input::-ms-input-placeholder {
  color: white;
  font-size: 14px;
}

.form-login input::placeholder {
  color: white;
  font-size: 14px;
}

.login-box button {
  margin-left: 5px;
}

.button-play {
  background-color: #fab005;
  border: none;
  border-radius: 7px;
  color: white;
  font-weight: 700;
  margin-left: 10px;
  padding: 7px 14px;
  -webkit-transition: 0.3s;
  transition: 0.3s;
}

.button-play:disabled {
  background-color: #ca9417;
  -webkit-transition: 0.2s;
  transition: 0.2s;
}

.settings-link {
  background-color: #fa5252;
  border: none;
  border-radius: 7px;
  color: white;
  font-weight: 700;
  padding: 7px 14px;
}


@media ( max-width : 999px ) {

  .img-login {
    -webkit-transition: 0.2s;
    transition: 0.2s;
    width: 350px;
  }

  .right .login-title {
    color: white;
    font-size: 40px;
    margin-bottom: 20px;
    text-align: center;
  }

  .button-play {
    margin-bottom: 10px;
    width: 50%;
  }

  .settings-link {
    width: 70%;
  }

  .right .login-title-span {
    color: white;
    font-size: 15px;
    margin-top: 0;
    text-align: center;
  }

}

@media ( max-width : 768px ) {

  .login-page {
    align-items: center;
    background-color: #84459c;
    display: flex;
    justify-content: center;
    min-height: 100vh;
  }

  .login-box {
    flex-direction: column;
  }

  .right .login-title {
    color: white;
    font-size: 40px;
    margin-bottom: -10px;
    text-align: center;
  }

  .right .login-title-span {
    color: white;
    font-size: 15px;
    margin-top: 50px;
    text-align: center;
  }

  .left, .right {
    padding: 0 40px;
    width: 100%;
  }

  .img-login {
    -webkit-transition: 0.4s;
    transition: 0.4s;
    width: 250px;
  }
}

.ranking {
  align-items: center;
  background-color: #fa5252;
  color: white;
  display: flex;
  flex-direction: column;
  justify-content: center;
  min-height: 100vh;
}

.title-button {
  align-items: center;
  display: flex;
  justify-content: space-evenly;
  text-align: center;
  width: 100%;
}

.players {
  background-color: #fab005;
  border-radius: 7px;
  display: flex;
  flex-direction: column;
  width: 60%;
}

.player-specs {
  display: flex;
  list-style: none;
  margin: 10px;
  padding: 0;
}

.player-specs li {
  padding: 1%;
}

.button-start {
  background-color: #511f52;
  border: none;
  border-radius: 7px;
  color: white;
  font-weight: 700;
  margin-left: 10px;
  padding: 7px 14px;
  -webkit-transition: 0.3s;
  transition: 0.3s;
}

.button-start:hover {
  background-color: #84459c;
  -webkit-transition: 0.2s;
  transition: 0.2s;
}

.ranking-img {
  border-radius: 7px 0 0 7px;
}

.img-render-ranking {
  border-radius: 50%;
}

.ranking-name {
  align-items: center;
  border-radius: 0;
  text-align: center;
}

.ranking-score {
  align-items: center;
  border-radius: 0 7px 7px 0;
}

.settings-page {
  background-image: url(CamilaDamasio.github.io/trivia-project/static/media/settings1-.4f869b72.jpg);
  background-size: cover;
  display: flex;
  flex-direction: column;
  height: 100vh;
  margin: 0;
  text-align: center;
}

.settings-text {
  align-items: center;
  color: rgb(185, 185, 185);
  display: flex;
  font-family: Quantico , sans-serif;
  font-size: 130%;
  height: 10%;
  justify-content: center;
  margin: 50px auto;
  text-align: center;
  width: 100vh;
}

.inputs-settings {
  align-content: center;
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  margin: 100px auto;
  width: 100%;

}

.categories, .dificulty, .type {
  background-color: #ffc02d;
  border-radius: 7px;
  font-size: 16px;
  font-weight: 600;
  padding: 5px;
  width: 20%;
}

.settings-button-home {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin: 20px auto;
  width: 90%;
}

.settings {
  background-color: #fa5252;
  border-radius: 7px;
  color: white;
  font-size: 16px;
  font-weight: 600;
  line-height: 25px;
  padding: 5px;
  text-align: center;
  width: 80%;
}

.settings:hover {
  background-color: #84459c;
  -webkit-transition: 0.2s;
  transition: 0.2s;
}

.red-border {
  border: 3px solid red;
  width: 100%;
}

.green-border {
  border: 3px solid rgb(6, 240, 15);
  border-radius: 7px;
}

.nextButton {
  visibility: hidden;
}

.trivia-page {
  background-color: #fab005;
  height: 100vh;
  overflow: hidden;
  width: 100%;
}

header {
  display: flex;
  height: 100px;
  margin: auto;
  width: 90%;
}

[ data-testid = 'header-profile-picture' ] {
  border-radius: 50%;
  margin: 10px auto;
}

[ data-testid = 'header-player-name' ] {
  color: #511f52;
  font-family: Quantico , sans-serif;
  font-size: 200%;
  margin: auto;
}

[ data-testid = 'header-score' ] {
  color: #228be6;
  font-family: Black Ops One , sans-serif;
  font-size: 50px;
  margin: auto;
  text-decoration: none;
}

.timer {
  align-items: center;
  background-color: blanchedalmond;
  border-radius: 7%;
  color: #c92a2a;
  display: flex;
  font-family: Black Ops One , sans-serif;
  font-size: 200%;
  height: 100px;
  justify-content: center;
  margin: auto;
  text-align: center;
  width: 180px;
}

.question-box {
  align-items: center;
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  height: 20%;
  justify-content: space-between;
  margin: auto;
  width: 80%;
}

.left-questions, .right-questions {
  align-content: center;
  padding: 0 20px;
  text-align: center;
}

.left-questions {
  background-color: #fa5252;
  border-radius: 5px;
  width: 88%;
}

.right-questions {
  background-color: #ffc02d;
  border: solid 2px #511f52;
  border-radius: 5px;
  margin: 5px;
  padding: 5px;
  width: 90%;
}

[ data-testid = 'question-category' ] {
  color: #511f52;
  font-family: Quantico , sans-serif;
  font-size: 150%;
  margin: 5px 0 0;
  text-align: center;
}

[ data-testid = 'question-text' ] {
  color: white;
  text-align: center;
}

ol {
  margin: 20px;
  padding: 0;
}

li {
  background-color: #511f52;
  border-radius: 7px;
  display: flex;
  height: 40px;
  justify-content: center;
  margin: 1% auto;
  text-decoration: none;
  width: 70%;
}

.answers {
  background-color: #511f52;
  border-radius: 7px;
  color: white;
  text-align: center;
  -webkit-transition: 0.2s;
  transition: 0.2s;
  width: 100%;
}

.answers:hover {
  background-color: #84459c;
  -webkit-transition: 0.2s;
  transition: 0.2s;
}

.btn-next-div {
  display: flex;
  justify-content: center;
  width: 100%;
}

[ data-testid = 'btn-next' ] {
  align-items: center;
  background-color: #82c91e;
  border-radius: 7px;
  color: black;
  font-weight: 700;
  margin-left: 10px;
  margin-top: -15px;
  padding: 7px 14px;
  -webkit-transition: 0.3s;
  transition: 0.3s;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media ( prefers-reduced-motion : no-preference ) {

  .App-logo {
    -webkit-animation: shake infinite 0.82s cubic-bezier(0.36, 0.07, 0.19, 0.97) both;
            animation: shake infinite 0.82s cubic-bezier(0.36, 0.07, 0.19, 0.97) both;
  }
}

.App-header {
  align-items: center;
  background-color: #282c34;
  color: white;
  display: flex;
  flex-direction: column;
  font-size: calc(10px + 2vmin);
  justify-content: center;
  min-height: 100vh;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes shake {

  10%, 90% {
    -webkit-transform: translate3d(-1px, 0, 0);
            transform: translate3d(-1px, 0, 0);
  }

  20%, 80% {
    -webkit-transform: translate3d(2px, 0, 0);
            transform: translate3d(2px, 0, 0);
  }

  30%, 50%, 70% {
    -webkit-transform: translate3d(-4px, 0, 0);
            transform: translate3d(-4px, 0, 0);
  }

  40%, 60% {
    -webkit-transform: translate3d(4px, 0, 0);
            transform: translate3d(4px, 0, 0);
  }
}

@keyframes shake {

  10%, 90% {
    -webkit-transform: translate3d(-1px, 0, 0);
            transform: translate3d(-1px, 0, 0);
  }

  20%, 80% {
    -webkit-transform: translate3d(2px, 0, 0);
            transform: translate3d(2px, 0, 0);
  }

  30%, 50%, 70% {
    -webkit-transform: translate3d(-4px, 0, 0);
            transform: translate3d(-4px, 0, 0);
  }

  40%, 60% {
    -webkit-transform: translate3d(4px, 0, 0);
            transform: translate3d(4px, 0, 0);
  }
}


